<template>
  <container :title="title">
    <list :isPager="false">
      <button slot="operation" class="btn btn-primary btn-sm delete" @click="mysqldump">
        <i class="fa fa-save"></i> 备份数据库</button>
      <thead slot="thead">
      <tr>
        <th><checkbox v-model="checked" :checked="tgCheck"/></th>
        <th>{{lang.id}}</th>
        <th>{{lang.table_name}}</th>
        <th>{{lang.table_rows}}</th>
        <th>{{lang.table_engine}}</th>
        <th>{{lang.create_time}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="(vol, index) in list">
        <td><checkbox  v-model="checkbox" :checked="checked" :value="vol['TABLE_NAME']"/></td>
        <td>{{index}}</td>
        <td><span class="label label-danger">{{vol['TABLE_NAME']}}</span></td>
        <td><span class="label label-success">{{vol['TABLE_ROWS']}}</span> 条记录</td>

        <td>{{vol['ENGINE']}}</td>
        <td>{{vol['create_time']}}</td>

      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List, VTd, Checkbox} from "@/components"
  import request from '@/utils/request'
  export default {

    components: {Checkbox, VTd,Container, List},
    data(){
      return{
        title:this.$t('database.index'),
        lang:this.$t('database'),
        checked:false,
        tgCheck:false,
        isOK: true
      }
    },
    watch:{
      checkbox(newValue){
        const length = newValue.length
        if (length === this.list.length){
          this.tgCheck = true
        }
        if (length === 0) {
          this.tgCheck = false
        }
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      },
      checkbox:{
        get(){
          return this.$store.state.table.checkbox
        },
        set(value){
          this.$store.commit('table/SET_CHECKBOX',value)
        }
      },
    },
    methods:{
      mysqldump(){
        if (!this.isOK) { return }
        const table_list = this.checkbox
        if (Array.isArray(table_list) && table_list.length <= 0){
          this.$toast.warn({
            title: '备份失败 ~',
            message: '请勾选需要备份的表~'
          })
          return ;
        }
        const data = {
          table_list: this.checkbox
        }
        request.post('/api/database/backup', data).then(resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
          }
          this.isOK = true
        })
      }
    },
    mounted(){
      const url = '/api/database/index'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



